import { useState, useEffect } from '@wordpress/element';
import { RichText } from '@wordpress/block-editor';
import { __ } from '@wordpress/i18n';
import produce from 'immer';

import { tabController } from '../../Components/Helper/functions';

import Settings from './Settings';
import Style from './Style';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { name, title, contacts, theme, isHeaderSep } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	const [activeIndex, setActiveIndex] = useState(0);

	const updateContact = (type, val, otherType = false) => {
		const newContacts = produce(contacts, draft => {
			if (otherType) {
				draft[activeIndex][type][otherType] = val;
			} else {
				draft[activeIndex][type] = val;
			}
		});
		setAttributes({ contacts: newContacts });
	}

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} activeIndex={activeIndex} setActiveIndex={setActiveIndex} updateContact={updateContact} />

		<div className={className} id={`bcbBusinessCard-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			<div className={`bcbBusinessCard ${theme}`}>
				<div className='header'>
					<RichText className='name' tagName='h3' value={name} onChange={val => setAttributes({ name: val })} placeholder={__('Person Name', 'business-card')} inlineToolbar />

					<RichText className='title' tagName='p' value={title} onChange={val => setAttributes({ title: val })} placeholder={__('Person Name', 'business-card')} inlineToolbar />

					{isHeaderSep && <span className='separator'></span>}
				</div>

				<div className='contacts'>
					{contacts?.length && contacts.map((contact, index) => {
						const { icon, text } = contact;

						return <div key={index} className={`contact ${index === activeIndex ? 'bcbNowEditing' : ''}`} onClick={() => setActiveIndex(index)}>
							{icon?.class && <i className={`icon ${icon?.class}`}></i>}

							<RichText className='text' tagName='p' value={text} onChange={val => updateContact('text', val)} placeholder={__('Text', 'icon-list')} inlineToolbar />
						</div>
					})}
				</div>
			</div>
		</div>
	</>;
};
export default Edit;