import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TabPanel, SelectControl, ToggleControl, __experimentalUnitControl as UnitControl, Button, Dashicon } from '@wordpress/components';
import produce from 'immer';

// Settings Components
import Title from '../../Components/Title';
import IconControl from '../../Components/IconControl';
import SeparatorControl from '../../Components/SeparatorControl';
import BtnGroup from '../../Components/BtnGroup';
import Background from '../../Components/Background';
import Typography from '../../Components/Typography';
import BColor from '../../Components/BColor';
import ColorsControl from '../../Components/ColorsControl';
import SpaceControl from '../../Components/SpaceControl';
import BorderControl from '../../Components/BorderControl';
import ShadowControl from '../../Components/ShadowControl';
import { tabController } from '../../Components/Helper/functions';
import { gearIcon } from '../../Components/Helper/icons';
import { pxUnit, perUnit, emUnit } from '../../Components/Helper/options';

import { themes, aligns, generalStyleTabs } from './utils/options';

const Settings = ({ attributes, setAttributes, activeIndex, setActiveIndex, updateContact }) => {
	const { contacts, alignment, width, theme, background, padding, border, shadow, headerAlign, headerMargin, nameTypo, nameColor, titleTypo, titleColor, isHeaderSep, headerSep, contactIconColors, contactTextTypo, contactTextColor } = attributes;

	const addContact = () => {
		setAttributes({
			contacts: [...contacts, {
				icon: {
					class: 'fas fa-globe'
				},
				text: 'https://example.com'
			}]
		});

		setActiveIndex(contacts.length);
	}

	const duplicateContact = e => {
		e.preventDefault();

		setAttributes({ contacts: [...contacts.slice(0, activeIndex), { ...contacts[activeIndex] }, ...contacts.slice(activeIndex)] });

		setActiveIndex(activeIndex + 1);
	}

	const removeContact = e => {
		e.preventDefault();

		setAttributes({ contacts: [...contacts.slice(0, activeIndex), ...contacts.slice(activeIndex + 1)] });

		setActiveIndex(0 === activeIndex ? 0 : activeIndex - 1);
	}

	const { icon = {} } = contacts[activeIndex] || {};

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <>
					<PanelBody className='bPlPanelBody help' title={__('Help', 'business-card')} initialOpen={false}>
						<div className='helpItem'>
							<a href='https://bblockswp.com/docs/business-card-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'business-card')}</a>
						</div>

						<div className='helpItem rateUs'>
							<a href='https://wordpress.org/support/plugin/business-card-block/reviews/#new-post' target='_blank' rel='noreferrer'>
								<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'business-card')}</span>
								<span>{__('We are new and we need your help to grow!🙏', 'business-card')}</span>
							</a>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody addRemoveItems editItem' title={__('Add or Remove Contacts', 'business-card')}>
						{null !== activeIndex && <>
							<h3 className='bplItemTitle'>{__(`Contact ${activeIndex + 1}:`, 'business-card')}</h3>

							<IconControl value={icon} onChange={val => updateContact('icon', val)} defaults={{ class: 'fas fa-globe' }} isSize={false} isColor={false} />

							<PanelRow className='itemAction mt20 mb15'>
								{1 < contacts?.length && <Button className='removeItem' label={__('Remove', 'business-card')} onClick={removeContact} ><Dashicon icon='no' />{__('Remove', 'business-card')}</Button>}

								<Button className='duplicateItem' label={__('Duplicate', 'business-card')} onClick={duplicateContact} >{gearIcon}{__('Duplicate', 'business-card')}</Button>
							</PanelRow>
						</>}

						<div className='addItem'>
							<Button label={__('Add New Contact', 'business-card')} onClick={addContact}><Dashicon icon='plus' />{__('Add New Contact', 'business-card')}</Button>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Layout Settings', 'business-card')} initialOpen={false}>
						<UnitControl label={__('Width:', 'business-card')} labelPosition='left' value={width} onChange={val => setAttributes({ width: val })} units={[pxUnit(), perUnit(), emUnit()]} />
						<small>{__('Keep width 0, to auto width.', 'business-card')}</small>

						<PanelRow className='mt20'>
							<Title className=''>{__('Theme:', 'business-card')}</Title>
							<SelectControl value={theme} onChange={val => {
								setAttributes({ theme: val });
								'default' === val && setAttributes({
									width: '600px',
									padding: { ...padding, vertical: '30px', horizontal: '25px' },
									headerAlign: 'start',
									headerMargin: { ...headerMargin, bottom: '30px' },
									isHeaderSep: true,
									headerSep: { ...headerSep, width: '20%' }
								});
								'theme1' === val && setAttributes({
									width: '650px',
									padding: { ...padding, vertical: '80px', horizontal: '30px' },
									headerAlign: 'start',
									headerMargin: { ...headerMargin, bottom: '0px' },
									isHeaderSep: false,
									headerSep: { ...headerSep, width: '20%' }
								});
								'theme2' === val && setAttributes({
									width: '650px',
									padding: { ...padding, vertical: '80px', horizontal: '30px' },
									headerAlign: 'end',
									headerMargin: { ...headerMargin, bottom: '0px' },
									isHeaderSep: true,
									headerSep: { ...headerSep, width: '50%' }
								});
							}} options={themes} />
						</PanelRow>
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Card', 'business-card')}>
						<Background label={__('Background:', 'business-card')} value={background} onChange={val => setAttributes({ background: val })} defaults={{ color: '#0000' }} />

						<SpaceControl className='mt20' label={__('Padding:', 'business-card')} value={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '30px', horizontal: '25px' }} />

						<BorderControl label={__('Border:', 'business-card')} value={border} onChange={val => setAttributes({ border: val })} />

						<ShadowControl label={__('Shadow:', 'business-card')} value={shadow} onChange={val => setAttributes({ shadow: val })} defaults={{ blur: '10px', color: '#4527a480' }} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Header', 'business-card')} initialOpen={false}>
						<PanelRow>
							<Title className=''>{__('Align:', 'business-card')}</Title>
							<BtnGroup value={headerAlign} onChange={val => setAttributes({ headerAlign: val })} options={aligns} isIcon={true} />
						</PanelRow>

						<SpaceControl className='mt20' label={__('Margin:', 'business-card')} value={headerMargin} onChange={val => setAttributes({ headerMargin: val })} defaults={{ side: 4, bottom: '30px' }} />

						<Typography className='mt20' label={__('Name Typography:', 'business-card')} value={nameTypo} onChange={val => setAttributes({ nameTypo: val })} defaults={{ fontSize: { desktop: 30, tablet: 26, mobile: 22 }, fontWeight: 700, textTransform: 'uppercase' }} produce={produce} />

						<BColor label={__('Name Color:', 'business-card')} value={nameColor} onChange={val => setAttributes({ nameColor: val })} defaultColor='#4527a4' />

						<Typography className='mt20' label={__('Title Typography:', 'business-card')} value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: { desktop: 18, tablet: 17, mobile: 16 } }} produce={produce} />

						<BColor label={__('Title Color:', 'business-card')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} defaultColor='#828282' />

						<ToggleControl label={__('Show Separator', 'business-card')} checked={isHeaderSep} onChange={val => setAttributes({ isHeaderSep: val })} />

						{isHeaderSep && <SeparatorControl className='mt20' value={headerSep} onChange={val => setAttributes({ headerSep: val })} defaults={{ width: '20%', height: '2px', style: 'solid', color: '#828282' }} />}
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Contact', 'business-card')} initialOpen={false}>
						<ColorsControl label={__('Icon Colors', 'business-card')} value={contactIconColors} onChange={val => setAttributes({ contactIconColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />

						<Typography label={__('Text Typography:', 'business-card')} value={contactTextTypo} onChange={val => setAttributes({ contactTextTypo: val })} defaults={{ fontSize: { desktop: 15, tablet: 15, mobile: 15 }, fontWeight: 500 }} produce={produce} />

						<BColor label={__('Text Color:', 'business-card')} value={contactTextColor} onChange={val => setAttributes({ contactTextColor: val })} defaultColor='#828282' />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('Business Card Alignment')} alignmentControls={[
				{ title: __('Business Card in left', 'business-card'), align: 'left', icon: 'align-left' },
				{ title: __('Business Card in center', 'business-card'), align: 'center', icon: 'align-center' },
				{ title: __('Business Card in right', 'business-card'), align: 'right', icon: 'align-right' }
			]} />

			<AlignmentToolbar value={headerAlign} onChange={val => setAttributes({ headerAlign: val })} describedBy={__('Header Text Align')} />
		</BlockControls>
	</>;
};
export default Settings;